import request from '../../util/request';
import common from '../../util/common';

export default {
    name: "Login",
    mounted() {
        uni.getStorage({
            key: 'userInfo',
            success: response1 => {
                uni.navigateTo({
                    url: '/pages/home/Home?userInfo=' + response1
                });
            },
            fail: reject1 => {
                // doNothing...
            }
        });
    },
    methods: {
        loginWx() {
            uni.login({
                success: response2 => {
                    // 获取用户信息
                    uni.getUserInfo({
                        provider: 'weixin',
                        success: response3 => {
                            uni.showLoading({
                                title: '登陆中'
                            });
                            let params = {
                                condition: {
                                    code: response2.code,
                                    avatar: response3.userInfo.avatarUrl,
                                    nickName: response3.userInfo.nickName,
                                    gender: common.getGender(response3.userInfo.gender)
                                }
                            };
                            request.post('/users/weixin/getUsersInfo', params).then(data => {
                                uni.hideLoading();
                                if (data.status === 200) {
                                    let result = JSON.stringify(data.data[0]);
                                    uni.setStorage({
                                        key: 'userInfo',
                                        data: result
                                    });
                                    uni.navigateTo({
                                        url: '/pages/home/Home?userInfo=' + result
                                    });
                                } else {
                                    uni.showToast({
                                        title: '获取用户信息失败',
                                        duration: 1000,
                                        icon: 'none'
                                    });
                                }
                            }).catch(e => {
                                uni.hideLoading();
                                uni.showToast({
                                    title: '获取用户信息失败',
                                    duration: 1000,
                                    icon: 'none'
                                });
                            })
                        },
                        fail: reject3 => {
                            // doNothing...
                        }
                    });
                },
                fail: response2 => {
                    uni.showToast({
                        title: '登录失败',
                        duration: 1000,
                        icon: 'none'
                    });
                }
            });
        }
    }
}
